	module.exports = {"data":{"site":{"siteMetadata":{"rootURL":"https://naoty.github.io"}},"markdownRemark":{"frontmatter":{"title":"モデルに記述する順番","time":"2017-12-02T23:02:00+0900","description":"Railsでモデルに記述する順番について考えてみた"},"html":"<p>他のメンバーがコードを読むときに知っておいてほしいことを意識して、以下の順に書いている。</p>\n<ol>\n<li>モジュール</li>\n<li>定数</li>\n<li>関連</li>\n<li>属性（<code>enum</code>, <code>attribute</code>など）</li>\n<li>コールバック</li>\n<li>バリデーション</li>\n<li>スコープ</li>\n<li>クラスメソッド</li>\n<li>インスタンスメソッド</li>\n<li>プライベートメソッド</li>\n</ol>\n<p>以下のコードは例のために書いてみたけど、動くかわからない。</p>\n<pre><code class=\"language-rb\">class Pokemon &#x3C; ApplicationRecord\n  include Tradable\n\n  INITIAL_HP_RANGE = 10..20\n\n  belongs_to :master\n  has_many :skills\n\n  enum gender: %i[male female]\n\n  before_validation :set_initial_hp, on: :create\n\n  validates :name, presence: true\n  validates :hp, numericality: { greater_than_or_equal_to: 0 }\n\n  scope :first_generation, -> { where(id: (1..151).to_a) }\n\n  def self.capture\n    # ...\n  end\n\n  def attack\n    # ...\n  end\n\n  private\n\n  def set_initial_hp\n    self.hp = rand(INITIAL_HP_RANGE)\n  end\nend\n</code></pre>","fileAbsolutePath":"contents/posts/3.md"}},"pathContext":{"id":"contents/posts/3.md absPath of file >>> MarkdownRemark"}}

